.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 100vh;
}

html {
  -webkit-animation: backgroundShit infinite 5s;
          animation: backgroundShit infinite 5s;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
}

.App-header {
  background-color: rgb(255, 0, 234);
}

.App-title {  
  margin: 0;
  font-size: 1.5em;
  -webkit-animation: titleShit infinite 1s;
          animation: titleShit infinite 1s;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@-webkit-keyframes titleShit {
  from { font-size: 1.5em; color: rgb(255, 0, 0); }
  to { font-size: 5em; color: rgb(0, 255, 157); }
}

@keyframes titleShit {
  from { font-size: 1.5em; color: rgb(255, 0, 0); }
  to { font-size: 5em; color: rgb(0, 255, 157); }
}

@-webkit-keyframes backgroundShit {
  from {background-color: rgb(255, 0, 234); }
  to {background-color: rgb(0, 204, 255); }
}

@keyframes backgroundShit {
  from {background-color: rgb(255, 0, 234); }
  to {background-color: rgb(0, 204, 255); }
}


